<template>
  <div id="app">
    <img class="banner" src="./assets/banner.png">
    <router-view class="view"></router-view>
    <div class="footer">
      <a v-bind:href="'reward?groupEventId='+params.groupEventId" title=""><div class=footer-nav>
        中奖名单
      </div></a>
      <a v-on:click="popInstruction()"><div class=footer-nav>
        活动规则
      </div></a>
      <a href="/instruction" title=""><div class=footer-nav>
        亲子年卡介绍
      </div></a>
    </div>
    <div class="pop-instruction" v-if="popIns">
      <div class="cover" v-on:click="closePop()">
      </div>
      <div class="instruction">
        <img src="./assets/close.png" alt="" v-on:click="closePop()">
        <p class="title">活动细则</p>
        <p>1.本活动仅限已关注“放学了”微信公众号的粉丝参加，活动期间取消关注则视为放弃获奖机会。</p>
        <p>2.本活动中，每位粉丝最多可创建100个新团，每位粉丝只能加入一次他人创建的团，团内凑满三人讥为满员团，活动期间创建新团和加入他人团的行为无法撤销。</p>
        <p>3.活动时间自2017年2月28日开始，组团成绩统计截止时间为2017年3月6日上午10点，活动结束后将从所有的满员团中随机抽取22个中奖图案，每个中奖团的3位粉丝都可以获得亲子年票优惠券一张。</p>
        <p>4.放学了微信官方公众号将在2月10日前公布中奖名单，并微信联系每位粉丝，发放亲子年票优惠券。</p>
        <p>5.粉丝凭优惠券可以在放学了商城以人民币1元购买该年票。</p>
      </div>
    </div>
  </div>
</template>

<script>
import Pub from './public.js'
export default {
  name: 'app',
  data () {
    document.title = "放学了"
    return {
      popIns:false,
      params:{}
    }
  },
  mounted: function() {
    var that = this;
    var params = {};
    var fxlUserInfo = {};
    if(localStorage.fxlUserInfo && localStorage.fxlUserInfo!=undefined){
    fxlUserInfo = JSON.parse(localStorage.fxlUserInfo)
    console.log(fxlUserInfo)
    }
    that.params = {
      groupEventId:Pub.services.getParam('groupEventId'),
      groupId:Pub.services.getParam('groupId'),
      ticket:fxlUserInfo.ticket,
      clientType:1
    }
    var getwx = function(res) {
      // 这里是处理正确的回调
          
          console.log(res)
    }
    Pub.services.getJssdk(getwx,that);

  },
  methods:{
    popInstruction:function(){
      this.popIns=true;
    },
    closePop:function(){
      this.popIns=false;
    },
  }
}
</script>

<style lang="scss">
*{
  box-sizing: border-box;
}
html,body{
  margin:0;
  min-height: 100%;
  width:100%;
  max-width: 800px;
  overflow:scroll;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width:100%;
}
.banner{
  width:100%
}
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

a {
  color: #42b983;
}
.footer-nav {
    display: inline-block;
    text-decoration: underline;
    color: #222;
}

.footer {
    display: flex;
    bottom: 0;
    position: fixed;
    width: 100%;
    padding: 18px;
    background: rgba(249,249,249,0.9);
    font-size: 18px;
}

.footer a {
    flex: 1;
    text-decoration: underline;
}
.instruction {
    position: fixed;
    top: 0;
    left: 0;
    background: #fff;
    height: 90%;
    z-index: 999;
    width: 90%;
    margin: 5%;
    padding: 10px 15px;
    border-radius: 5px;
    text-align: left;
    overflow: scroll;
}

.instruction img {
    position: absolute;
    right: 10px;
    height: 28px;
    width: 28px;
}

.instruction .title {
    font-size: 16px;
    color: #222;
    font-weight: 700;
    margin: 30px;
    text-align: center;
}

.instruction p {
    font-size: 15px;
    color: #333;
}
</style>



